let page = 1
let str = ''
export default {
    onLoad(){
        console.log('123+++')
        page = 1
        this.getshoucanglst()
    },
    data(){
        return {
            coponIndex:1 ,
            counpList:[],
            loadingType:0,
            isCheck:false, // 复选框是否显示
        }
    },
    onReachBottom:function(){
        // 上拉加载更多
        if(this.loadingType == 1 || this.loadingType == 2) { return false }
        this.loadingType = 1

        page++
        this.getshoucanglst()
    },
    onNavigationBarButtonTap(e) {
        console.log(999)
        let pages = getCurrentPages();
		let page = pages[pages.length - 1];











    },
    methods: {
        /**
         * 批量删除 取消收藏
         */
        delect(){
            this.$showModal({content:'确认取消收藏吗？'}).then(res=>{
                if(res.confirm) {
                    let collArr = []
                    this.counpList.forEach((el,index)=>{
                        if(el.icon ) {
                           collArr.push(el.id)
                        } 
                    })
                    this.$http.getcancelShowcang({
                        filter:this.coponIndex,
                        coll_id:collArr.join(',')
                    }).then(result =>{
                        if(result.status == 200) {
                            this.$toast(result.mess)
                            page = 1
                            this.loadingType = 0
                            this.counpList = []
                            this.getshoucanglst()
                        }
                    })
                }
            })
        },
        /**
         * 复选框
         */
        getkeysCheck(id){
            if(!this.isCheck && this.coponIndex == 1) {
                this.getnav(`/pages/homeSon/shop_details?id=`+id)
                return false
            }
            if(!this.isCheck && this.coponIndex == 2) {
                this.getnav(`/pages/homeSon/storeDetails?shop_id=`+id)
                return false
            }
            this.counpList.forEach((el,index)=>{
                if(el.id == id) {
                    el.icon = !el.icon
                } 
            })
        },
        clickactive(i) {
            if(this.coponIndex == i) { return}
            this.coponIndex = i
            page = 1
            this.loadingType = 0
            this.counpList = []
            this.getshoucanglst()
            
        },
        getshoucanglst(){
            if(page == 1) {
                this.$loading()
            }
            this.$http.getshoucanglst({filter:this.coponIndex,page:page}).then(res=>{
                if(res.status == 200) {
                    res.data.forEach(element => {
                        element.icon = false
                    });
                    if(res.data.length == 0) {
                        this.loadingType = 2
                        return false
                    }
                    this.counpList = [...this.counpList,...res.data]
                    this.loadingType = 0
                }
            })
        },

    },

}
